body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#content_bin {
    margin-left: 175px;
    padding-top: 15px;
    width: calc( 100vw - 205px );
}
#root {
    height: 100vh;
    overflow-y: scroll;
}

#mob-stats .field,
#mob-stats .field input,
#mob-stats .field textarea {
    width: 100%;
    max-width: 100%;
}

.field label {
    text-align: left;
}

#areas-list .list,
#mobs-list .list {
    text-align: left;
}

#object-stats .field,
#object-stats .field input,
#object-stats .field textarea {
    width: 100%;
    max-width: 100%;
}

.field label button.ui.icon.button {
    padding: 0;
    display: inline;
    background: transparent;
    color: rgba(0,0,0,.87);
}
#dashboard p {
    margin: 0 auto;
    max-width: 900px;
    text-align: center;
}

#mobs-list .list .item .ui.text.menu a {
    padding: 0;
}

#mobs-list .list .item .ui.text.menu a:after {
    content: "|";
    padding: 5px;
}

#mobs-list .list .item .ui.text.menu a:last-of-type:after {
    content: "";
    padding: 0;
}

#mobs-list .list .item .ui.text.menu {
    margin: 0;
    padding: 0;
}


.ui.segment.scale.visible.transition {
    text-align: center;
}

.ui.container {
	padding: 2em;
	border: 1px solid #ececec;
	background-color: #fff;
	margin: 2em 0;
}

.ui.container .header {
	overflow: visible;
}

.ui.container .header button {
    position: absolute;
    top: -7px;
    color: #fff;
    min-width: 150px;
    background-color: #ff5900;
    text-shadow: 1px 1px 1px #282c34;
}
header {
    padding: 0 5px;
    width: 70px;
}
.App-header {
    align-items: center;
    background-color: #282c34;
    color: white;
    display: flex;
    flex-direction: column;
    font-size: calc(10px + 2vmin);
    justify-content: center;
    min-height: 100vh;
    width: 150px;
}

    .App-header > a {
        height: auto;
        position: absolute;
        top: 10px;
        width: 150px;
    }
    .App-header a {
        color: #fff;
        display: block;
        font-family: 'Permanent Marker', cursive;
        margin: 0 0 3em 0;
        text-decoration: none;
    }

    .App-header nav {
        max-width: 100%;
    }

        .App-header nav .ui.vertical.text.menu .item {
            color: #fff;
            font-size: 20px;
            margin-bottom: 1em;
            margin-left: 25px;
        }

.Sidebar {
    position: absolute;
}
